
.userList{
    flex: 4;
}

.userListUser{
    display: flex;
    align-items: center;
}
.userListImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    margin-left: 10px ;
    object-fit: cover;
    border: 2px solid #2563eb;
}
.userListEdit{
    border: none;
    border-radius: 10px;
    padding: 5px 10px;
    color: #fff;
    background-color: #3bb077;
    margin-left: 20px;
    cursor: pointer;
}
.userListDelete{
    color: red;
    cursor: pointer;
    vertical-align: -6px
}
.MuiDataGrid-cell{
    text-align: right !important;
   
}
